import { AbiConfig, Constants } from '@/Constants';
import { getChainData } from '@/web3/tools';
import WalletConnectProvider from '@walletconnect/web3-provider';
import { Contract, ethers, providers } from 'ethers';
import mitt from 'mitt';
import { onMounted, onUnmounted, ref } from 'vue';
import Web3Modal from 'web3modal';

type ProviderEvents = {
  accountsChanged: string;
  chainChanged: number;
  disconnect: [number, string];
};
export const defaultChain = getChainData(Constants.ChainId);
export const providerBus = mitt<ProviderEvents>();
export const CurrentWalletEnv = { address: '', chainId: 0 };

// https://github.com/Web3Modal/web3modal#web3modal
export const web3Modal = new Web3Modal({
  theme: 'dark',
  network: defaultChain.network,
  cacheProvider: true,
  providerOptions: {
    // https://docs.walletconnect.org/
    walletconnect: {
      package: WalletConnectProvider,
      options: {},
    },
  },
});
export const useWallet = () => {
  const wallet = ref(CurrentWalletEnv);
  const update = (arg: any) => {
    if (CurrentWalletEnv.address) CurrentWalletEnv.address = ethers.utils.getAddress(CurrentWalletEnv.address);
    wallet.value = { ...CurrentWalletEnv };
  };
  const _wallet = (address: string) => update((CurrentWalletEnv.address = address));
  const _chain = (chainId: number) => update((CurrentWalletEnv.chainId = chainId));
  onMounted(async () => {
    providerBus.on('accountsChanged', _wallet);
    providerBus.on('chainChanged', _chain);
    if (!wallet.value.chainId) (await getProvider()).getNetwork().then((net) => update((CurrentWalletEnv.chainId = net.chainId)));
    if (!wallet.value.address) (await getProvider()).listAccounts().then((accs) => update((CurrentWalletEnv.address = accs[0] || '')));
  });
  onUnmounted(() => {
    providerBus.off('accountsChanged', _wallet);
    providerBus.off('chainChanged', _chain);
  });
  return { wallet };
};

export const getExProvider = async (): Promise<WalletConnectProvider> => {
  if (window.ethereum && window.ethereum.isMetaMask) return window.ethereum;
  const exProvider = new WalletConnectProvider({
    rpc: getChainData(Constants.ChainId).rpc_url,
  });
  await exProvider.enable();
  return exProvider;
};

let lastProvider: providers.Web3Provider | null = null;
export const getProvider = async () => {
  if (lastProvider) return lastProvider;
  const exProvider = await getExProvider();
  exProvider.on('accountsChanged', (address: string[]) => {
    CurrentWalletEnv.address = address[0] || '';
    providerBus.emit('accountsChanged', address[0] || '');
  });
  exProvider.on('chainChanged', (chainId: number) => {
    CurrentWalletEnv.chainId = chainId;
    providerBus.emit('chainChanged', chainId);
  });
  exProvider.on('disconnect', (code: number, reason: string) => {
    CurrentWalletEnv.address = '';
    providerBus.emit('disconnect', [code, reason]);
  });
  lastProvider = new providers.Web3Provider(exProvider);
  return lastProvider;
};

export const StaticWeb3Read = new providers.JsonRpcProvider(defaultChain.rpc_url);
export const getContractHandler = async (type: keyof typeof Constants.Contract, address = '', sign = false) => {
  if (!address) address = Constants.Contract[type];
  if (!sign) return new Contract(address, AbiConfig[type], StaticWeb3Read);
  const prov = await getProvider();
  if (!CurrentWalletEnv.address) {
    await web3Modal.connect();
    const accs = await prov.listAccounts();
    CurrentWalletEnv.address = accs[0] || '';
    providerBus.emit('accountsChanged', accs[0] || '');
    if (!CurrentWalletEnv.address) throw 'not address';
  }
  if (CurrentWalletEnv.chainId !== defaultChain.chain_id) {
    await web3Modal.connectTo('0x' + defaultChain.chain_id.toString(16));
    const net = await prov.getNetwork();
    CurrentWalletEnv.chainId = net.chainId;
    providerBus.emit('chainChanged', net.chainId);
    if (CurrentWalletEnv.chainId !== defaultChain.chain_id) throw 'not chain';
  }
  const signer = prov.getSigner();
  return new Contract(address, AbiConfig[type], signer);
};

export const waitTransaction = async (res: any) => {
  const provider = await getProvider();
  return new Promise<providers.TransactionReceipt>(async (resolve, reject) => {
    try {
      provider.once(res.hash, resolve);
    } catch (e) {
      const msg = ContractActionError(e);
      reject(msg);
    }
  });
};

export const ContractActionError = (e: any) => {
  if (!e) return '';
  if (typeof e === 'string') return e;
  if (typeof e !== 'object') return String(e);
  if (e.code === 4001) return '';
  let message = String(e.message) || String(e) || '';
  if (e.data && typeof e.data === 'object') {
    if (e.data.message) message = message + ' : ' + e.data.message;
  }
  return message;
};
