import { HttpClient, WsClient } from 'tsrpc-browser';
import { serviceProto, ServiceType } from './shared/protocols/serviceProto';

export function getWsClient(): WsClient<ServiceType> {
  return new WsClient(serviceProto, {
    server: 'ws://127.0.0.1:1027',
    // Remove this to use binary mode (remove from the server too)
    // json: true,
    logger: console,
  });
}
export function getHttpClient(): HttpClient<ServiceType> {
  return new HttpClient(serviceProto, {
    server: 'http://127.0.0.1:1026',
    // Remove this to use binary mode (remove from the server too)
    json: true,
    logger: console,
    timeout: 60000,
  });
}
