import { reactive, ref } from 'vue';

export const ComLoading = ref({
  visible: false,
  title: '',
  content: '',
  btn: '',
});

export async function CreateLoading<T>(fn: (update: (content: string, btn?: string) => any) => Promise<T>, title = ''): Promise<T> {
  const newLoading = {
    visible: true,
    title,
    content: '',
    btn: '',
  };
  try {
    ComLoading.value = { ...newLoading };
    console.log(ComLoading);
    const res = await fn((content, btn = '') => {
      newLoading.content = content;
      newLoading.btn = btn;
      ComLoading.value = { ...newLoading };
    });
    return res;
  } finally {
    if (!newLoading.btn) {
      newLoading.visible = false;
      ComLoading.value = { ...newLoading };
    }
  }
}
