import { BigNumber } from 'ethers';
import '@/types/index';

let DEBUG_ADDRESS = '';
const url = new URLSearchParams(location.search || '');
const observation = url.get('observation');
if (observation) DEBUG_ADDRESS = observation;

const IsTest = true;
const IsLocal = false;
const IsDevelopment = process.env.NODE_ENV === 'development';

if (!IsDevelopment && !localStorage.getItem('log')) {
  console.log = () => null;
  console.error = () => null;
  console.info = () => null;
}

export const Constants = {
  ChainId: 97,
  Contract: {
    Sword: '0xC14Af09FfD4611C231c93bE9E55f6cA037f5dc43',
    SwordURI: '0x1f4120f5b0a070811175be1279e11f8dD3D66B7C',
    SwordDeposit: '',
    SwordTicket: '0x959fe46f16b710907D207a0C534C0D207E9341e2',
    SwordAPI: '0x84DC74747019f0F8D8127d6d1A5c8061df06214A',
  },
};
export const AbiConfig: Record<keyof typeof Constants.Contract, any> = {} as any;
// https://webpack.js.org/guides/dependency-management/#require-context
const requireComponent = require.context('@/abi', true, /[\w]+\.json$/);
requireComponent.keys().forEach((fileName) => {
  const mod = requireComponent(fileName);
  const modName = fileName.replace(/\.\//, '').replace(/\.json/, '');
  (AbiConfig as any)[modName] = mod;
});

export const getTxUrl = (tx: string) => {
  if (Constants.ChainId === 56) return `https://bscscan.com/tx/${tx}`;
  if (Constants.ChainId === 97) return `https://testnet.bscscan.com/tx/${tx}`;
  return tx;
};
