<template>
  <a-modal v-model:visible="ComLoading.visible" :title="ComLoading.title" :keyboard="false" :maskClosable="false" :closable="false" @ok="handleOk">
    <template v-slot:footer>
      <div class="flex flex-center">
        <a-button type="primary" @click="CloseLoading" :loading="!ComLoading.btn">{{ ComLoading.btn || 'OK' }}</a-button>
      </div>
    </template>
    <div class="flex flex-center">
      <div v-html="ComLoading.content"></div>
    </div>
  </a-modal>
</template>

<script setup>
import { computed, reactive, ref } from 'vue';
import { ComLoading } from './ComLoadingState';

const handleOk = () => {
  ComLoading.value.visible = false;
};
const CloseLoading = () => {
  ComLoading.value.visible = false;
};
</script>
